@import url('https://fonts.googleapis.com/css2?family=Lato&family=Montserrat:wght@300;500;600;700;800&display=swap');


* {
  padding: 0;
  margin: 0;
}



.intro {
  height: 46.375rem;

  background: black;
  filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.4));

  color: white;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.lab {
  height: 46.375rem;
  width: 22.5rem;
  position: absolute;
  z-index: -1;
  animation: opacity 600ms;
  overflow: visible;
}

.logo {
  text-align: center;
  padding: 0.5rem 1rem 0.5rem 1rem;
  border: 2px solid #FFFF;

  margin-top: 2.5rem;
  animation: opacity 600ms both;
  animation-delay: 0.80s;
}



.highlight_text {
  font: 600 1.25rem "Montserrat", sans-serif;
  width: 13.875rem;
  text-align: center;
  animation: opacity 600ms both;
  animation-delay: 1.40s;
}

.span {
