  border: 0;
  border-radius: 0.5rem;
  color: var(--white-color);
  font: 700 1rem "Montserrat", sans-serif;

  cursor: pointer;
  animation: opacity 600ms both;
  animation-delay: 1.60s;
}

.sequence {
